@font-face {
  font-family: "Ostrich Bold";
  src: url("resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-bold.eot");
  src:
  url("resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-bold.woff") format("woff"),
  url("resources/fonts/ostrich-sans-master/OstrichSans-Bold.otf") format("opentype"),
  url("resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-bold.svg#filename") format("svg");
      
  }
  
  @font-face {
    font-family: 'Ostrich Regular';
    src: url('resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-regular.eot');
    src: local('â˜º'), url('resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-regular.woff') format('woff'), url('resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-regular.ttf') format('truetype'), url('resources/fonts/ostrich-sans-master/webfonts/ostrich-sans-regular.svg') format('svg');
