/*
Colors:
  button Default: :#EBD128
  Button selected: #D1B13D

  custon fonts: Ostrich Regular,Ostrich Bold

*/
@media (min-width:501px) {




#main-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  overflow-x:hidden;
  margin:0px;
  list-style-type:none;
}



::-webkit-scrollbar {
width:0;
scroll-behavior: smooth;
}

h1{
  text-align: center;
  padding:5px;
  font-size:400%;

  color:#FFF;
  min-width:300px;
  max-width:640px;
  width:30%;
  
  margin:40px 0px 20px 50px;
  background: #f00;
  border-radius:40px;
  letter-spacing:5px;
}





.navBar{
  position:fixed;
  z-index:1;

  text-align: center;
  margin: 0px;
  width:100%;

  left:50%;
  transform: translate(-50%,0);
  background-color:#333333;

  float:left;
}
.logo{
  width:70px;
  float:left;
  margin-left:20px;
  cursor:pointer;
}
  .navList li{
    list-style: none;
    margin:10px;
    padding:10px 10px 5px 10px;

    border:solid 2px #333333;
    border-bottom-color:#f00;
    cursor:pointer;

    font-size:180%;

    cursor:pointer;

    color:#FFF;

    float:right;
    border-radius:10px 10px 0px 0px;
    
  }
    .navList li:hover{
     /* background-color:#EBD128;*/
      border-color:#f00;
      border-radius:10px;
      color:#FFF;
      font-weight: 600;
      border-radius:10px;
    }
    .navListSelected:hover, .navListSelected:not(:hover){
      background-color:#f00;
      border-color:#f00;
      color:#FFF;
      font-weight: 600;
      border-radius:10px;
    }

    .navSocialMediaIcon{

      width:30px;
      cursor:pointer;
    }



.lindiName{
  position:inherit;
  margin-top:-90px;
  margin-bottom:50px;
  font-size:250%;
  color:#FFF;
  text-align: left;
  letter-spacing: 5px;

  
}



.headerText{
  background-image:linear-gradient(rgba(26, 188, 156, 0),rgba(26, 188, 156, 0)) , url("../resources/images/HeaderBackground.jpg");
 /*background-image:url("../resources/images/HeaderBackgroundedited.png");*/
  background-repeat:no-repeat;
  background-size:cover;
  background-position: right;

  padding-top:150px;
  padding:170px 20px 20px 20px;
  height: 100vh;
}
  .headerText h2{
    padding:20px;
  }
    .headerBold{
      font-family:Ostrich Bold;
      font-size:1000%;
      color:#f00;
    }
    .headerReg{
      font-size:300%;
    }



.shop ul{
  margin-left:0vw;
  margin-right:0px;  
  padding-left:30px;
  

}
  .shop ul li{
    display:inline-block;
    border: 2px solid #000;
    border-radius:20px;

    width:300px;
    height:300px;
    margin:0px 20px 20px 20px;
    
    
    background-size:cover;
    background-position:center;
    

  }
  .shopButton:hover{
    opacity:1;
  }
  .shopButton{
    position:relative;
    background:linear-gradient(rgba(0,0,0,.6),rgba(0, 0, 0, 0.6));
    height:100%;
    opacity:0;
    transition: opacity .5s;
  }
  .shopButtonPackage{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    
  }
  .shopButton p{
    border: 2px solid #F00;
    margin:20px;
    display:inline-block;
    padding:10px;

    background-color:#F00;
    border-radius:10px;
    cursor:pointer;
  }
    .shopButton p:hover{
      font-weight:bold;
    }
  .shopButton a{
    font-size:200%;
    color:#FFF;
    cursor:pointer;
  }

.gallery ul{
  margin:0px 50px 0px 50px;
  padding:10px 20px 20px 20px;
}
  .gallery li{
      display:inline-block;
      margin: 10px 10px 0px 0px;

      border:2px solid #000;
      padding:10px;
      background-color:#FFF;
      border-radius:20px;
    }
      .gallery img{
        width:200px;
        border:2px solid #000;
        border-radius:20px;
      }



.eventsTitle{
  padding-bottom:40px;
  
}
  .event{
    border: 2px solid #FFF;
    display:inline-block;
    margin: 5px;
    padding:7px;

    
    background:#FFF;
    border:2px solid #000;
    border-radius:20px;
    text-align: center;

    margin-left:50px;


  }
    .event p{
      font-size:150%;
    }
    .event img{
      border:2px solid #000;
      height:300px;
      border-radius:10px;
      cursor:pointer;
    }
    .eventDescription{
      border: 2px solid #000;
      border-radius:10px;
    }
      .event p a{
        font-size:120%;
        border-bottom:2px solid #FFF;
        cursor:pointer;
      }



.aboutMe{
  padding-bottom:20px;
  background-size:cover;
  background-position:center;
  

}
  .aboutMe img{
    border-radius:20px;
    width:30%;
  }
  .aboutMe p{
    display:inline-block;
    width:70%;
    vertical-align: top;
    font-size:150%;

    background-color: #FFF;
    border-radius:20px;
    padding:15px;
  }




.contactMe{
padding-top: 20px;
padding-bottom:10px;
height:100vh;
}
.contactMe p{
  font-size:200%;
  margin:0 20px 20px 50px;
  color:#FFF;
}
  .contactMeForm input, .contactMeForm textArea, .button{
    position:relative;
    left:50%;
    transform: translate(-50%,0);
    
    box-shadow:none;
    border-width:0;
    outline:none;

    margin-bottom:10px;
    border:2px solid #FFF;
    border-radius:20px;
    padding:10px;


    width:700px;
    font-size:140%;
    display:block;

    font-family:monospace;
    cursor:text;
    

    transition:background-color .5s ,border .5s, color .5s;
  }
    .contactMeForm input:focus, .contactMeForm textArea:focus,.contactMeForm input:hover, .contactMeForm textArea:hover{
      border-color:#333333;
    }

      .formContent{
        min-height:400px;
      }
  .button:hover{
    background-color:#333333;
    border-color:#333333;
    color:#FFF;
  }
  .button{
    color:#333333;
    cursor:pointer;
    background-color:#FFF;
  }



.socialMedia ul{
  background-color:#000;
}
.socialMedia{
  position:absolute;
  width:100%;
  background-image:linear-gradiant(#333333,#333333);
  border:2px solid #333333;
}
.socialMedia:not(:hover), .socialMedia:hover{
  background-color:#333333;
}
  .socialMedia ul li{
    margin:10px;
    list-style:none;
    float:right;

  }
  .socialMedia p{
    float:left;
    color:#FFF;
    padding:30px;
    font-size:120%;
  }

    .socialMediaIcon{
      width:60px;
      cursor:pointer; 
    }

}























